
.calc{
    width: 100vw;
    height: 100vh;
    display: flex;
    background-image: url('./iphon.jpg');
    background-repeat: no-repeat;
    background-position: center;
}

.bt{
    
    background-color: rgb(0, 0, 0);
    width: 21em;
    height: 35em;
    display: block;
    position: relative;
    color: white;
    justify-content:center;
    text-align: center;
    border-radius: 1em;
    margin:  auto;
}

h1{
    text-align: end;
    font-size: 5em;
    height: 1.2em;
    width: 4em;
}

button {
    width: 1.9em;
    height: 1.9em;
    font-size: 2.2em;
    border-radius: 2em;
    border: none;
    margin: 0.2em;
    background-color:rgb(59, 59, 59) ;
    color: rgb(255, 255, 255);
    transform: scale(1.1);
}

button:active, .op:active , .op2:active , .ellipse:active{
    cursor: pointer;
    filter: brightness(200%);
    
}

.elipse{
    width: 4.2em;
    
}

.op{
    width: 1.9em;
    height: 1.9em;
    font-size: 2.2em;
    border-radius: 2em;
    margin: 0.2em;
    background-color:rgb(212, 145, 0) ;
    color: rgb(255, 255, 255);
}

.op2{
    width: 1.9em;
    height: 1.9em;
    font-size: 2.2em;
    border-radius: 2em;
    margin: 0.2em;
    background-color:rgb(187, 187, 187) ;
    color: rgb(0, 0, 0);
}